.footer{
    background-color: var(--eerie-black-1);
}
.footer hr {
    color: var(--mainColor) !important;
    width: 90%;
}

.section_padding{
    padding-bottom: 0.5rem;
}
@media screen and (max-width: 1150px) {
    .section_padding{
        padding: 4rem 2rem;
    }
}

.sb_footer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    width: 100%;
    margin-bottom: 2rem;
    padding: 0 7rem;
}
.sb_footer hr{
    color: var(--mainColor) !important;
    width: 100%;
}
@media screen and (max-width: 850px) {
    .sb_footer{
        flex-direction: column;
    }
    .sb_footer{
        padding: 0 0;
    }
    .sb_footer-links{
        margin: 1rem 0;
    }
}

.sb_footer-links{
    width: 50%;
    margin: 1rem;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    color: var(--mainColor);
    text-align: center;
    height: auto;
}
.social {
    min-height: 150px;
}
.social h2{
    margin: 0 0 2rem 0;
} 
.social-media{
    display: flex;
    gap: 2rem;
    flex-direction: row;
    padding: 0 2rem;
    margin: auto;
}
.social-media button {
    background-color: transparent;
    border: 1px solid white;
    border-radius: 1.5rem;
    height: 3rem;
    width: 3rem ;
    margin: auto;
}
.social-media i{
    width: 100%;
    height: 100%;
    margin: auto;
    padding: 0;
    position: relative;
    top: 25%;
    left: 50%;
    transform: translate(-50%);
}
.sb_footer-links h2{
    font-size: 1.8rem;
    line-height: 14px;
    margin-bottom: 1.5rem;
}

.sb_footer-links p, .sb_footer-links a, .sb_footer-links span{
    font-size: 1.2rem;
    line-height: 15px;
    padding: 0.5rem;
    cursor: pointer;
    margin: 0;
}
.sb_footer-links i{
    font-size: 1.4rem;
    color: var(--white);
}
.sb_footer-links a, .sb_footer-links span{
    padding: 0;
}
.sb_footer-links p:hover{
    background-color:  var(--textColor);
    color: var(--eerie-black-1);
    border-radius: 7px;
}
@media screen and (max-width: 1000px) {
    .sb_footer-links{
        width: 100%;     
        margin: 1rem 0;
        text-align: left;
    }
    .sb_footer-links h2{
        font-size: 1.5rem;
    }
    .sb_footer-links p, .sb_footer-links span{
        font-size: var(--fontSizePhone);
    }
    .sb_footer-links i{
        font-size: 1.5rem; 
        padding-right: 1rem;
    }
    .social {
        height: auto;
    }
    .social h2{
        margin:auto;
    } 
    .social-media{
        padding: 0 4rem;
    }
    .social-media i{
        padding: 0;
    }
}
.links{
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: flex-start;
}
.links p{
    padding: 0;
}
.links-color{
    font-size: 1.5rem;
    color: var(--white);
}
@media screen and (max-width: 1000px) {
    .links-color{
        font-size: 1.5rem;
    }
}
a{
    color: var(--mainColor);
    flex-direction: row;
}


.sb_footer-below{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 0.2rem;
}



.sb_footer-copyright p{
    font-size: 13px;
    line-height: 15px;
    color: var(--mainColor);
    font-weight: 600;
}
@media screen and (max-width: 1000px) {
    .sb_footer-below{
        flex-direction: column;
        justify-content: left;
    }
}
  

@media screen and (max-width: 850px) {
    .sb_footer-heading h4{
        font-size: 44px;
        line-height: 50px;
    }
}
    
@media screen and (max-width: 1150px) {
    .section_padding{
        padding: 4rem 2rem;
    }
    .sb_footer-heading h4{
        font-size: 34px;
        line-height: 42px;
    }
    .sb_footer-links{
        padding: 0 0;
    }
    .sb_footer-links div{
        margin: 1rem 0;
    }
    .sb_footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }

    .sb_footer-below{
        flex-direction: column;
        justify-content: left;
    }
}

@media screen and (max-width: 400px) {
    .sb_footer-heading h4{
        font-size: 38px;
        line-height: 38px;
    }
}